@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
@import url(https://fonts.googleapis.com/css?family=Quicksand|Raleway);
body {
  margin: 0;
  padding: 0;
  /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family:  Roboto Slab, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  background-image : url(/Dinnergise/static/media/background1.5d978871.jpg);
}

/*    SIDEBAR   */
.wrapper {
    display: flex;
    align-items: stretch;
}

#sidebar {
    position: fixed;
    height : 100vh;
    z-index : 999;
    min-width: 200px;
    max-width: 285px;
    background: #222;
    color: #fff;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
#sidebar.active {
    margin-left: -285px!important;
}

#sidebar .sidebar-header {
    padding: 20px;
}

#sidebar ul.components {
    padding: 5px 0;
}
#sidebar ul li a {
    padding: 1px 33px;
    font-size: 1em;
    font-weight: 600;
    display: block;
    color: #808080;
    font-family: 'Quicksand', sans-serif;
    text-decoration: none!important;
}
#sidebar ul li a:active{
  color : white;
}
#content {
    min-height: 100vh;
    min-width : 99vw;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    padding-left: 285px;
}
hr{
  width : 75%;
  border : 1px solid #808080!important;
  opacity: 0.3;
}
.About{
  display: none!important;
}

@media screen and (max-width: 768px){
    #content{
      padding-left : 0;
    }
    /*#sidebar {
        margin-left: -285px;
    }*/
    .sideMenuClass{
      display: none;
    }
    #sidebar{
      min-width : 100px;
      max-width : 150px;
      margin-left : 0;
    }
    #sidebar.active {
        margin-left: 0;
    }
    #sidebarCollapse span {
        display: none;
    }
}


/*     MAIN CONTENT   */

.Header{
  padding: 1% 1%!important;
}
.header-content{
  position: relative;
  background: -webkit-linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,.6)),url(/Dinnergise/static/media/main-content.d0b236c4.jpg) 0 -50px no-repeat;
  background: linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,.6)),url(/Dinnergise/static/media/main-content.d0b236c4.jpg) 0 -50px no-repeat;
  background-repeat: no-repeat;
  background-size : cover;
  height : 55vh;
}
.main-img-content{
  position: absolute;
  top: 35%;
  left: 50%;
  -webkit-transform: translate(-50%, -35%);
          transform: translate(-50%, -35%);
  color : white;
  text-align : center;
}
.main-head-txt{
  font-weight : 600;
  font-size : 3em;
}
.main-head-para{
  font-weight: 500;
  font-size: 1.3em;
  color : #909090;
}
.main-btn{
   margin-top: 3%;
   color : white;
   border: 2px solid white!important;
   border-radius: 30px!important;
   background-color : inherit;
   padding : 1.5% 5%!important;
   font-weight: bold!important;
}
.main-btn:hover{
  background-color : red;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  border : 2px solid red!important;
}
.dots{
  position: absolute;
  top : 160%;
  left : 64%;
  -webkit-transform: translate(-100%, -100%);
          transform: translate(-100%, -100%);
  width : 30%;
}
.dot {
  cursor : pointer;
  height: 15px;
  width: 15px;
  background-color: inherit;
  border  : 2px solid #808080;
  border-radius: 50%;
  margin-left  : 3%;
  display: inline-block;
}
.dot .active{
  background-color : #404040!important;
}
.res-header .row{
  margin   : 0;
  padding: 0!important;
}
.res-header{
  z-index : 999;
  background-color : #222;
  padding: 1%;
  display: none;
}
.res-header .img-fluid{
  width : 30vw;
  padding : 1%;
}
.menu-icon{
  float : right;
  padding: 5%;
}
.menu-icon-bg{
  padding: 5%;
  width : 3.5vw;
  height : 0.2vh;
  background-color:  white!important;
  margin : 6px 0;
}
@media screen and (max-width : 768px){
  .Header{
    padding: 0%!important;
  }
  .header-content{
     height : 82vh;
  }
  .main-img-content{
    width : 85vw;
    margin-top : 5%!important;
  }
  .main-head-txt{
    font-size : 2em;
  }
  .main-head-para{
    font-size: .9em;
    color : white;
    opacity: 0.7;
  }
  .dots{
     margin-top: 20%;
  }
  .res-header{
    display: block!important;
    height : 10vh;
  }
  .res-header .row .img-fluid{
    width : 35vw;
  }
  .menu-icon{
     padding : 0!important;
     margin-top : -10%!important;
  }
  .menu-icon-bg{
      width : 5vw;
  }
  .main-btn{
    margin-top : 5%;
    padding : 3% 5%!important;
  }
}
@media screen and (max-width : 1170px){
    .Header{
      padding: 0;
    }
    .main-img-content{
      width : 90vw;
    }
    .menu-icon{
       padding : 0!important;
       margin-top : -15%;
    }
}

.section-content .row{
  padding: 0!important;
  margin : 0;
}
.section-content{
  overflow: hidden;
  margin-top : 2%;
  background-color: white;
  padding-bottom : 2%;
  padding-top: 2%;
}
.section-content .text{
    text-align : left;
    padding : 2%;
}
.section-content .text h4{
  font-weight: bold;
  font-size: 1.5vw;
}
.section-content .text .icon{
    width : 1.2vw;
    opacity : 0.6;
}
 .box{
  margin-top : 1%;
  PADDING: 10%!important;
  background-color : #800080;
  color: white;
}
.banner-section, .banner-section .row{
  padding: 0!important;
  margin : 0!important;
}
.banner-section .row{
  margin-left : 6%!important;
}
.banner-section h2{
  text-align : center;
}
.banner-section-box{
    background-color : white;
}
.btn-banner-section-custom{
    margin : 2%;
    text-align : center;
    background-color : grey;
    border-radius : 35px!important;
    padding : 0.5% 4%!important;
}
.boxImg{
  width : 6vw;
}
.orderBtn{
  margin-top : 3%;
  background-color:#800080;
  border-radius : 35px!important;
  color :white;
  font-weight: bold;
  margin-bottom : 2%!important;
}
@media screen and (max-width: 768px){
      .section-content .text h4{
            font-size : 4.5vw;
      }
      .section-content .text p{
          font-size : 0.8em;
      }
      .section-content .text .icon{
          width : 4.5vw;
      }
      .section-content .box{
        text-align : center;
      }
      .box{
         padding: 2%!important;
         width : 80vw;
         margin-left : 5%;
      }
      .boxImg{
        width : 30vw;
      }
      .banner-section .row{
         margin : 0!important;
      }
      .banner-section-box{
          width : 92vw!important;
          margin : 4%;
      }
}

